.policy-main--container{
    text-align: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding: 60px 0;
}

.policy-primary--heading{
    text-transform: uppercase;
    letter-spacing: 3px;
    margin: 80px auto;
    color:#189AB4;
    border-bottom: 5px solid #189AB4;
    width: 300px;
}

.policy-secondry--heading{
text-transform: uppercase;
letter-spacing: 1px;
padding: 0 120px;
margin: 25px 0;
color: rgba(0,0,0,0.7);
}

.policy-para{
padding: 0 150px;
line-height: 1.8;
text-align: justify;
margin-bottom: 40px;
font-weight: 500;
color: rgba(0,0,0,0.6);
}
.privacy-seven{
    text-align: center;
}
.regards{
    letter-spacing: 1px;
    color:#189AB4;
    font-size:22px;
}
.terms-heading{
    width: 340px;
}
.refund-container{
    height: 62.5vh;
}
@media screen and (max-width:700px) {
    .policy-primary--heading{
        font-size: 30px;
        width:280px;
    }
    .policy-secondry--heading{
        padding: 0 30px;
        }
        
        .policy-para{
        padding: 0 40px;
        }
        .terms-heading{
            font-size: 26px;
            width: 285px;
            margin: 60px auto;
        }  
    .refund-heading{
        font-size: 30px;
        width: 270px;
        margin: 60px auto;
    }      
}